  color: rgba(0, 0, 0, 0.5);
  font-size: 14px;
  line-height: 1.2;
  font-weight: 400;
  margin: 10px 0;
  width: 100%;
  display: block;
  border: none;
  border: 1px solid #f2f2f2;
  resize: none; }

textarea {
