.button {
  display: block;
  width: 100%;
  font-weight: 500;
  color: white;
  font-size: 16px;
  padding: 20px 60px 20px 30px;
  background: $button-green;
  transition: all .25s $ease;
  position: relative;
  border: none;
  cursor: pointer;
  &:hover {
    color: white;
    background: $button-dark-green; }
  &:before {
    display: block;
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    border: 2px solid white;
    right: 25px;
    top: calc(50% - 5px);
    transform: rotate(45deg);
    border-bottom: none;
    border-left: none; } }

