  padding: 50px 10px;
  h2 {
    margin: 50px 0 35px; }
  .flex-container {
    display: inline-flex;
    justify-content: space-between;
    width: 100%;
    &__item {
      width: 50%;
      min-height: 200px;
      @include bs;
      border: 1px solid #f2f0f0;
      padding: 30px 50px 30px 30px;
      position: relative;
      overflow: hidden;
      &:nth-child(1) {
        border-right: 0; }
      &:before {
        display: block;
        content: '';
        @include css3-prefix(border-radius, 50%);
        transition: all .25s $ease;
        position: absolute;
        width: 180px;
        height: 180px;
        background: $green;
        // bottom: -90px;
        // right: -40px;
        bottom: -200px;
        right: -200px;
        @include background-gradient-radial(#82cb1d, #9de833); } }

    &__info {
