      font-weight: 400;
      position: relative;
      z-index: 2;
      transition: all .25s $ease; }
    &__number {
      color: #f2f2f2;
      font-weight: 700;
      transition: all .25s $ease;
      position: absolute;
      font-size: 58px;
      bottom: -14px;
      right: 40px;
      opacity: 0.7;
      line-height: 1;
      padding: 0; }
    &:nth-child(3) {
