// BEGIN input[type=checkbox] popup
.container-checkbox {
  display: block;
  position: relative;
  padding: 5px 0 0 35px;
  cursor: pointer;
  font-size: 14px;
  color: black;
  font-weight: 400;
  user-select: none;
  min-height: 25px;
  margin: 20px 0; }

.container-checkbox a {
  color: black;
  font-weight: 400;
  text-decoration: underline; }

.container-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer; }

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #f2f2f2; }

.container-checkbox:hover input ~ .checkmark {
  background-color: #ccc; }

.container-checkbox input:checked ~ .checkmark {
  background-color: #8ecd35; }

.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

.container-checkbox input:checked ~ .checkmark:after {
  display: block; }
.checkmark {
  outline: none; }
.checkmark.error {
  outline: 1px solid red; }

.container-checkbox .checkmark:after {
  left: 9px;
  top: 4px;
  width: 6px;
  height: 12px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg); }

// END input[type=checkbox] popup
// BEGIN input[type=file] popup
.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1; }

.inputfile + label {
  max-width: 100%;
  font-size: 14px;
  font-weight: 500;
  color: $green;
  white-space: nowrap;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  margin: 5px 0 20px;
  padding: 0;
  border-bottom: 2px dashed $green; }

.inputfile + label i,
.inputfile + label svg {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  fill: currentColor;
  margin-top: -0.25em;
  margin-right: 0.25em; }

.inputfile:focus + label,
.inputfile.has-focus + label,
.inputfile + label:hover {
  color: $button-dark-green; }

