.white-popup {
  cursor: default;
  a.button {
    margin-top: 50px; }
  .error-label-container {
    position: relative;
    #phone-error,
    #name-error,
    #email-error,
    #personal-info-error {
      position: absolute;
      display: block;
      bottom: calc(100% + 7px);
      right: 0;
      background: #dd2a1b;
      padding: 12px;
      color: white;
      font-size: 12px;
      line-height: 1.4;
      max-width: 150px;
      &:after {
        position: absolute;
        display: block;
        width: 10px;
        height: 10px;
        content: '';
        background: #dd2a1b;
        bottom: -5px;
        right: 15px;
        transform: rotate(45deg); } }
    label.error.valid {
      display: none !important; }
    #personal-info-error {
      right: auto;
      left: 0;
      &:after {
        right: auto;
        left: 7px; } } } }
.phone-container {
